body{
    /* background-color: rgb(238, 238, 238); */
    background-color: rgb(184, 183, 183);
}
.product{
    display: flex;
    flex-direction: column;
    background-color: white;
    z-index: 10;
    width: fit-content;
    margin: 0.5rem 2rem;
    padding: 1.7rem 3rem;
    transition: 0.3s;
}
.product:hover{
    transform: scale(1.02);
    box-shadow:
  0.1px 0.5px 2.7px rgba(0, 0, 0, 0.155),
  0.2px 1.4px 6.9px rgba(0, 0, 0, 0.222),
  0.4px 2.8px 14.2px rgba(0, 0, 0, 0.278),
  0.7px 5.8px 29.2px rgba(0, 0, 0, 0.345),
  2px 16px 80px rgba(0, 0, 0, 0.5)
;
}
.product-image{
    display: flex;
    text-align: center;
    flex-direction: column;
}
.product-image>img{
    width: 15rem;
    height: 16rem;
    object-fit: cover;
}
.title{
    font-weight: bolder;
    font-size: 1.5rem;
    font-family: sans-serif;
}
.price>.amount{
    font-size: 1.2rem;
}
.add{
    background-color: rgb(247, 188, 62);
    border: 0.7px black solid;
    padding: 0.4rem;
    margin: 1.5rem 0;
    cursor: pointer;
    transition: 0.2s;
    width: 50%;
    color: black;
}
.add:hover{
    background-color: rgb(233, 187, 87);;
}
.ratings{
    color: rgb(255, 217, 0);
}
