.cover-image{
    width: 100%;
    height: 50vh;
    object-fit: cover;
    mask-image: linear-gradient(to bottom,rgb(0,0,0,1),rgb(0,0,0,0.1));
    z-index: -1;
    margin-bottom: -6rem;
}
.product-container{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    width: 100vw;
}
.centerLoading{
    display: flex;
    justify-content: center;
}
.loading{
    text-align: center;
    align-items: center;
}