.checkoutItem{
    display: flex;
    flex-direction: column-reverse;
}
.products{
    display: flex;
    margin: 1rem 1rem;
    align-items: center;
}
.price{
    font-weight: 500;
}
.products > img{
    max-width: 15rem;
    max-height: 20rem;
    margin: 0 1rem;
}
.rating-icon{
    color: rgb(255, 217, 0);
}
.remove-btn{
    padding: 0.3rem 0.7rem;
    border: 1px solid black;
    background-color: rgb(247, 188, 62) ;
    cursor: pointer;
}
.remove-btn:hover{
    background-color: rgb(248, 215, 25);
}