.payment{
    text-align: center;
    margin: 2rem auto;
    font-family: sans-serif;
}
.payment_container{
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    background-color: white;
}
.form2{
    flex: 0.6;
    display: grid;
    grid-template-columns: 1fr 1fr;
    text-align: start;
}
.input{
    margin: 1rem 0;
    padding: 0;
    display: flex;
    flex-direction: column;
}
.payment_title>p{
    text-align: center;
    /* color: lightgray; */
    background-color: lightgray;
    padding: 1rem 0rem;
    margin-bottom: 1rem;
}
.checkout_container{
    margin: 2rem 0;
    text-align: start;
}
.input>input{
    width: 70%;
    font-size: larger;
    font-weight: 300;
}
.payment_icons{
    display: flex;
    align-items: center;
}
.payment_icons > input{
    margin: 0 1rem;
}
.buy_btn{
    padding: 0.7rem 1.7rem;
    background-color: rgb(247, 188, 62);
    border: none;
    font-size: 1rem;
}
.buy_btn:hover{
    background-color: rgb(233, 183, 76);
}
.adress_info{
    width: fit-content;
    padding: 1rem;
    color: red;
    font-weight: 500;
    text-decoration: underline;
}