.order{
    text-align: center;
    font-family: sans-serif;
    background-color: lightgray;
}

.order_container{
    text-align: center;
    
}
.single_container{
    background-color: white;
    padding: 2rem;
    margin: 2rem auto;
    width: 70%;
}
.product_order_container{
    display: flex;
    justify-content: space-around;
    align-items: center;
    text-align: start;
    margin: 1rem 0;

}
.product_order_info{
    font-size: 2rem;
}
.product_order_container>img{
    width: 10rem;
}
.react-icon{
    color: rgb(255, 217, 0);
}
.date{
    font-size: larger;
    margin-bottom: 2rem;
}