.header{
    display: flex;
    background-color: rgb(39, 43, 65);
    /* background-color: #131921; */
    align-items: center;
    justify-content: space-between;
    position: sticky;
    top: 0;
    z-index: 1000;
}
.logo{
    padding: 0 2rem;
    width: 120px;
    height: 60px;
    flex: 0.1;
}
.header_search{
    display: flex;
    /* width: 40%; */
    flex: 0.5;
}
.search-input{
    width: 200%;
    height: 1.7rem;
}
.search-btn{
    background-color: rgb(255, 178, 13);
    padding: 0 0.8rem;
    font-size: 1.3rem;
    border: none;
}
.header-nav{
    flex: 0.4;
    color: white;
    display: flex;
    justify-content: space-evenly;
}
.header-option{
    display: flex;
    flex-direction: column;
    margin: 0 0.5rem;
    text-align: center;
    text-decoration: none;
}
.header-option-1{
    color: rgb(172, 172, 172);
}
.header-option-2 > button{
    color: white;
    font-weight: 900;
    background-color: transparent;
    border: none;
    font-family: sans-serif;
    cursor: pointer;
}
.header-option-2 > a{
    text-decoration: none;
    color: white;
    font-weight: 700;
}
.cart{
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.cart-icon{
    color: white;
    font-size: 2rem;
}
.cart-2{
    margin: 0 0.2rem;
}