.checkout{
    display: flex;
    background-color: white;
}
.checkout-left{
    flex: 0.7;
    /* text-align: center; */
    background-color: white;
}
.checkout-ad{
    width: 100%;
    object-fit: contain;
}
.checkout-right{
    flex: 0.3;
    background-color: rgb(238, 238, 238);
    padding: 2rem 4rem;
    margin: 2rem 2rem;
}
.checkout-right > p{
    margin: 1rem 0;
    font-size: 1.2rem;
    font-weight: 500;
}
.checkout-right > button{
    width: 100%;
    background-color: rgb(247, 188, 62);
    color: black;
    border: 1.7px solid black;
    padding: 0.7rem 1rem;
    /* transition: 0.2s; */
    cursor: pointer;
}
.checkout-right > button:hover{
    background-color: rgb(241, 176, 35);
}
.gift-input{
    margin: 1rem 0 ;
    display: flex;
    align-items: center;
    
}
.checkbox{
    padding: 0;
    margin: 0 1rem;
    width: fit-content;
}
.label{
    margin: 0;
    padding: 0;
}
h2{
    text-transform: capitalize;
}