body{
    background-color: rgb(250, 250, 250);
    font-family: sans-serif;
}
.login{
    text-align: center;
    height: 100vh;
}
.login-logo{
    max-width: 10rem;
    max-height: 5rem;
}
.login-container{
    display: flex;
    flex-direction: column;
    text-align: start;
    width: 25%;
    height: fit-content;
    border: 1px solid lightgray;
    padding: 1.2rem;
    margin: 0 auto;
}
h3{
    font-size: 2rem;
    font-weight: 500;
    margin: 0 0 1.5rem 0;
}
input{
    width: 80%;
    height: 1.5rem;
    /* font-size: 1.3rem; */
}
.form{
    display: flex;
    flex-direction: column;
}
.login-btn{
    width: fit-content;
    border: 1px solid black;
    background-color: #f0c14b;
    padding: 0.5rem 1rem;
    margin: 1rem 0;
    cursor: pointer;
}
.terms{
    width: 80%;
    font-weight: 500;
}
.new-acc{
    text-align: center;
    font-size: small;
    margin: 1rem 0;
}
.create-btn{
    padding: 0.2rem 0.7rem;
    cursor: pointer;
}
.new{
    margin: 0 0 0.7rem 0;
}